import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import { Alert } from 'reactstrap';
import {getWardSet,getDepartmentSet,getBedTypeSet,getHospitalSet,getRoomSet,getBedSet, getLedSet,addLedRegister} from './../Apis.js'
import * as Yup from 'yup';

import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import { timingSafeEqual } from 'crypto';

var QRCode = require('qrcode.react');

class AddLedRegister extends Component {
    constructor(props) {
        super(props);
        this.state = {
            patient_id:'',
            emr_id:'',
            hospital_id:'1',
            depart_id:'1', 	
            ward_id:'1', 	
            room_id:'1', 	
            bed_id:'1',
            title:'',
            description:'',
            showForm:false,
            msgType:'success',
            massege:'',
            QrValues:'',
            showQR:false
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
        setSubmitting(false);
        this.addLedRegister(values,setSubmitting)
        // this.setState({showQR:true})
        console.log(values)
        }
        addLedRegister=async(values,setSubmitting)=>{
            console.log(values)
            const responseData= await addLedRegister(values,values.id);
            console.log(responseData,'response')
            if(responseData.data.message)
            {
                this.setState({ visible:true,massege:'Smart Screen Add Failed '+responseData.message,msgType:'danger'})
                setSubmitting(false);
            }
            else{
                this.setState({ visible:true,msgType:'success',massege:'Smart Screen Successfully'})
                setSubmitting(false);
                this.props.history.push('/leds')
                return;
            }
        }
         
        componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSet:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 

            const responseDataDepart= await getDepartmentSet();
            console.log(responseDataDepart,'response')
            if(responseDataDepart.status == 200){
                this.setState({departmentSets:responseDataDepart.data.result})
            } else if (responseDataDepart.status == 404) {
                console.warn(responseDataDepart.data.message);
            } 
            
            const responseDataWard= await getWardSet();
            console.log(responseDataWard,'response')
            if(responseDataWard.status == 200){
                this.setState({wardSets:responseDataWard.data.result})
            } else if (responseDataWard.status == 404) {
                console.warn(responseDataWard.data.message);
            } 

            const responseDataBedType= await getBedTypeSet();
            console.log(responseDataBedType,'response')
            if(responseDataBedType.status == 200){
                this.setState({bedtypeSets:responseDataBedType.data.result})
            } else if (responseDataBedType.status == 404) {
                console.warn(responseDataBedType.data.message);
            } 
            const responseDataLeds= await  getLedSet();
            console.log(responseDataLeds,'asdf')
            if(responseDataLeds.status == 200){
                console.log('asdf',responseDataLeds)
                this.setState({ledListSet:responseDataLeds.data.result})
            } else if (responseDataLeds.status == 404) {
                console.warn(responseDataLeds.data.message);
            } 
           
            const responseDataRoom= await getRoomSet();
            console.log(responseDataRoom,'response')
            if(responseDataRoom.status == 200){
                this.setState({roomSets:responseDataRoom.data.result,showForm:true})

            } else if (responseDataRoom.status == 404) {
                console.warn(responseDataRoom.data.message);
            } 
            const responseDataBed= await getBedSet();
            console.log(responseDataBed,'response')
            if(responseDataBed.status == 200){
                this.setState({bedSets:responseDataBed.data.result})
            } else if (responseDataBed.status == 404) {
                console.warn(responseDataBed.data.message);
            } 
        }

       
    render() {
        console.log(this.state.visible)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                    </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Smart Screen Register</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                            initialValues={{
                                name: this.state.name,
                                Id:this.state.id,
                                hospital_id: '1',
                                depart_id: '1',
                                ward_id: '1',
                                room_id: '1',
                                bed_id: '1',
                                status:this.state.status
                                
                            }}
                        
                            onSubmit={this.handleSubmit}
                            render={formProps => {
                                return(
                                <Form className="form-horizontal">
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Select Smart Screen:</Label>
                                        </Col>
                                        <Col  xs="12" md="9">
                                            <Field 
                                           required={true}
                                           component="select" 
                                            name="id" 
                                            placeholder="Name" 
                                            id="text-input"
                                            className="form-control"
                                            >
                                            {this.state.ledListSet&&this.state.ledListSet.map((led, index) =>
                                            <option key={index} value={led.id}>{led.name}</option>
                                                )}
                                                </Field> 
                                            <ErrorMessage name="id" />
                                        </Col>                                        
                                    </FormGroup>
                                    
                                  
                             
                                   
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Hospital Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            disabled={true}
                                              component="select" 
                                            name="hospital_id" 
                                            placeholder="Hospital Name " 
                                            className="form-control"
                                            >
                                                 {this.state.hospitalSet&&this.state.hospitalSet.map((hospital, index) =>
                                                 <option key={index} value={hospital.id}>{hospital.name}</option>
                                            )}
                                            </Field> 
                                            {/* <ErrorMessage name="phone" /> */}
                                        </Col>                                      
                                    </FormGroup>
                                     <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Department Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                              component="select" 
                                            name="depart_id" 
                                            placeholder="Department Id" 
                                            className="form-control"
                                            >
                                                 {this.state.departmentSets&&this.state.departmentSets.map((department, index) =>
                                                 <option key={index} value={department.id}>{department.name}</option>
                                            )}
                                            </Field> 
                                             <ErrorMessage name="phone" /> 
                                         </Col>                                         
                                    </FormGroup> 
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Ward Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             component="select" 
                                            name="ward_id" 
                                            placeholder="Ward Id" 
                                            className="form-control"
                                            >
                                                      {this.state.wardSets&&this.state.wardSets.map((ward, index) =>
                                                        <option key={index} value={ward.id}>{ward.name}</option>
                                                    )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                         
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Room No:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             required={true}
                                             component="select" 
                                            name="room_id" 
                                            placeholder="Room NO" 
                                            className="form-control"
                                            >
                                            {this.state.roomSets&&this.state.roomSets.map((room, index) =>
                                              <option key={index} value={room.id}>{room.room_no}</option>
                                          )}
                                            </Field> 
                                            <ErrorMessage name="phone" />
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">                                                           
                                            <Label htmlFor="text-input">Bed No:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                             component="select" 
                                            name="ward_id" 
                                            placeholder="Ward Id" 
                                            className="form-control"
                                            >
                                                      {this.state.bedSets&&this.state.bedSets.map((bed, index) =>
                                                        <option key={index} value={bed.id}>{bed.bed_no}</option>
                                                    )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                          
                                    </FormGroup>
                                   
                              
                                    <Col md="3" style={{float:'right'}}>
                                    <button 
                                        type="submit"
                                        className="btn btn-success btn-block custonbtn" 
                                        
                                        disabled={formProps.isSubmitting}>
                                       Register
                                    </button>
                                    </Col>
                                    </Form>
                                );
                            }}

                            />}
                             {/* <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert> */}
                        </CardBody>
                    </Card>
                    </Col>
                    {/* <Col xs="4">
                    <Card className={'qrsec'}>
                 
                        {this.state.showQR? <div><h3>Scan this QR Code from your Patient App</h3><QRCode renderAs={'svg'} size={250} value={JSON.stringify(this.state.QrValues)} /></div>:
                        <h1>Click On generate QR</h1>
                       }
                    
                    </Card></Col> */}
                </Row>
            </div>
            
        );
    }

}

export default AddLedRegister;
